import { findCodeNameInArr, isObjEmpty, getCodeName, removeEl, select, isElEmpty } from './utils';
import { State, InstanceState } from './State';
import { Building } from './Buildings';
import { showTooltip, hideTooltip } from './Tooltip';
import { upgrades as u } from './upgrades/index';
import { spend } from '.';
import { UpdatesState } from './Updates';

export interface Upgrade {
    name: string;
    flavorText?: string;
    price: number;
    onBuy: {
        increaseBuildingProductionMultiplier: [string, number];
    };
}

const Upgrade = function (upgrade) {
    this.name = upgrade.name;
    this.codeName = getCodeName(upgrade.name);
    this.desc = upgrade.desc || buildUpgradeDesc(upgrade);
    this.flavorText = upgrade.flavorText;
    this.price = upgrade.price;
    this.onBuy = upgrade.onBuy;
    this.isLocked = upgrade.isLocked === false ? false : true;
    this.isOwned = upgrade.isOwned === false ? false : false;

    this.buy = (event: MouseEvent) => {
        if (spend(this.price)) {
            this.isOwned = true;

            if (this.onBuy) {
                if (this.onBuy.increaseBuildingProductionMultiplier) {
                    State.buildings[
                        this.onBuy.increaseBuildingProductionMultiplier[0]
                    ].productionMultiplier += this.onBuy.increaseBuildingProductionMultiplier[1];

                    UpdatesState.updateOPS = true;
                }
            }

            const upgradeEl: EventTarget = event.target;
            const upgradesContainerEl = select('.upgrades-container');
            removeEl(upgradeEl as HTMLElement);
            if (isElEmpty(upgradesContainerEl)) UpdatesState.updateTabContent = true;
        }
    };

    this.mousemove = (event: MouseEvent): void => showTooltip(event, { type: 'upgrade', upgrade: this });
};

const buildUpgradeDesc = (upgrade) => {
    const building: Building = State.buildings[upgrade.onBuy.increaseBuildingProductionMultiplier[0]];

    let desc = `Increases the production multiplier of ${building.namePlural} by ${upgrade.onBuy.increaseBuildingProductionMultiplier[1]}`;

    return desc;
};

const upgrades = [...u.schoolUpgrades, ...u.farmUpgrades, ...u.quarryUpgrades, ...u.churchUpgrades, ...u.factoryUpgrades];

export const instantiateUpgrades = (): void => {
    const state = JSON.parse(localStorage.getItem('state'));

    const builtUpgrades = {};

    if (state) {
        if (!isObjEmpty(state.upgrades)) {
            for (let key in state.upgrades) {
                builtUpgrades[key] = new Upgrade(state.upgrades[key]);
            }
        }
    } else {
        upgrades.forEach((upgrade) => {
            builtUpgrades[upgrade.name] = new Upgrade(upgrade);
        });
    }

    State.upgrades = builtUpgrades;
};
